'use client';

import dynamic from 'next/dynamic';

import { cn } from '@kapoo/headless-ui-pack/lib/utils';

import Wave from '../../atom/Wave';
import Img from '../Img';

import type { CSSProperties, DetailedHTMLProps, HTMLAttributes } from 'react';

const WaveSky = dynamic(() => import('../WaveSky'), { ssr: false });

export interface FooterProps extends DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement>
{
	/**
	 * 시간대
	 */
	readonly hour?: Hour;

	/**
	 * 기본 색상
	 */
	readonly mainColor?: CSSProperties['backgroundColor'];

	/**
	 * 바디 색상
	 */
	readonly bodyColor?: CSSProperties['background'];
}

/**
 * 푸터 molecule 컴포넌트 반환 메서드
 *
 * @param {FooterProps} param0: FooterProps
 *
 * @returns {React.JSX.Element}
 */
export default function Footer({ hour, mainColor, bodyColor, className, children, ...props }: FooterProps): React.JSX.Element
{
	return (
		<footer className={cn('mt-3', className)} data-component="Footer" {...props}>
			<div className="relative flex h-72 flex-col overflow-hidden">
				<WaveSky hour={hour} />

				<div className="animate-boat absolute -bottom-5 left-[10%]">
					<Img
						alt="ship"
						height={100}
						src="https://github.com/user-attachments/assets/e2a11c89-57af-4944-9068-bf237369f8b3"
						width={100}
					/>
				</div>

				<div className="absolute bottom-0 w-full">
					<Wave fillColor={mainColor} height={30} width="100%" />
				</div>
			</div>

			<div style={{ background: bodyColor ?? mainColor }}>
				<div className="w-full">
					{children}
				</div>
			</div>
		</footer>
	);
}