'use client';

import { type PropsWithChildren, useLayoutEffect } from 'react';
import reactGA from 'react-ga4';

export interface GoogleAnalyticsProviderProps extends PropsWithChildren
{
	/**
	 * GA4 키
	 */
	readonly gaKey: string;
}

/**
 * GA 프로바이더 organism 컴포넌트 반환 메서드
 *
 * @param {GoogleAnalyticsProviderProps} param0: GoogleAnalyticsProviderProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function GoogleAnalyticsProvider({ gaKey, children }: GoogleAnalyticsProviderProps): React.JSX.Element
{
	useLayoutEffect(() =>
	{
		reactGA.initialize(gaKey);
	}, [ gaKey ]);

	// eslint-disable-next-line @react/jsx-no-useless-fragment
	return <>{children}</>;
}