/**
 * 앱 네비게이션 organism 컴포넌트
 *
 * @author RWB
 * @since 2025.03.25 Tue 17:36:31
 */

'use client';

import { BookText, FlaskConical, Home, MessageSquareHeart } from 'lucide-react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { useState } from 'react';

import { useIntersectionObserver } from '@kapoo/common';
import Typography from '@kapoo/global-ui-pack/atom/Typography';
import Img from '@kapoo/global-ui-pack/molecule/Img';
import Navigation from '@kapoo/global-ui-pack/organism/Navigation';
import Button from '@kapoo/headless-ui-pack/components/new-york/Button';

export interface AppNavigationProps
{
	/**
	 * 타이틀
	 */
	readonly title: string;

	/**
	 * 버전
	 */
	readonly version: string;
}

/**
 * 앱 네비게이션 organism 컴포넌트 반환 메서드
 *
 * @returns {React.JSX.Element} JSX
 */
export default function AppNavigation({ title, version }: AppNavigationProps): React.JSX.Element
{
	const pathname = usePathname();

	const [
		isTopState,
		setIsTopState
	] = useState(true);

	useIntersectionObserver('#topper', (entry) =>
	{
		setIsTopState(entry.isIntersecting);
	});

	return (
		<>
			<div id="topper" />

			<Navigation className="z-40" link="/" logo="/favicon.ico" title="itcode.dev" transparent={isTopState}>
				<div className="flex size-full flex-col">
					<div className="flex flex-col items-center">
						<div className="relative m-4 aspect-square w-full max-w-20 rounded-lg shadow-lg">
							<Img alt="blog.itcode.dev" src="/favicon.ico" fill />
						</div>
					</div>

					<div className="flex flex-1 flex-col gap-2">
						<Link href="/">
							<Button className="w-full justify-start" variant={pathname === '/' ? 'secondary' : 'ghost'}>
								<Home color="gold" />
								<Typography variant="large">홈</Typography>
							</Button>
						</Link>

						<Link href="/posts">
							<Button className="w-full justify-start" variant={pathname.startsWith('/posts') ? 'secondary' : 'ghost'}>
								<BookText color="dodgerblue" />
								<Typography variant="large">게시글</Typography>
							</Button>
						</Link>

						<Link href="/projects">
							<Button className="w-full justify-start" variant={pathname.startsWith('/projects') ? 'secondary' : 'ghost'}>
								<FlaskConical color="springgreen" />
								<Typography variant="large">프로젝트</Typography>
							</Button>
						</Link>

						<Link href="/comments">
							<Button className="w-full justify-start" variant={pathname.startsWith('/comments') ? 'secondary' : 'ghost'}>
								<MessageSquareHeart color="hotpink" />
								<Typography variant="large">방명록</Typography>
							</Button>
						</Link>
					</div>

					<div className="flex flex-col items-center justify-center gap-1">
						<Typography variant="small">{title}</Typography>
						<Typography variant="muted">{version}</Typography>
					</div>
				</div>
			</Navigation>
		</>
	);
}