import { VisuallyHidden } from '@radix-ui/react-visually-hidden';

import type { SheetContentProps, SheetProps } from '@kapoo/headless-ui-pack/components/new-york/Sheet';
import Sheet from '@kapoo/headless-ui-pack/components/new-york/Sheet';

import type { ReactNode } from 'react';

export interface SidebarProps extends SheetProps
{
	/**
	 * 사이드바 오픈 위치
	 *
	 * @default {string} left
	 */
	readonly side?: SheetContentProps['side'];

	/**
	 * 트리거 영역
	 */
	readonly trigger?: ReactNode;
}

/**
 * 사이드바 molecule 컴포넌트 반환 메서드
 *
 * @param {SidebarProps} param0: SidebarProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function Sidebar({ side = 'left', trigger, children, ...props }: SidebarProps): React.JSX.Element
{
	return (
		<Sheet.Root data-component="Sidebar" {...props}>
			{trigger}

			<Sheet.Content side={side}>
				<VisuallyHidden>
					<Sheet.Title />
					<Sheet.Description />
				</VisuallyHidden>

				{children}
			</Sheet.Content>
		</Sheet.Root>
	);
}