import Link from 'next/link';

import { cn } from '@kapoo/headless-ui-pack/lib/utils';

import Typography from '../../atom/Typography';
import Img from '../Img';

import type { DetailedHTMLProps, HTMLAttributes, ReactNode } from 'react';

export interface HeaderProps extends DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement>
{
	/**
	 * 로고
	 */
	readonly logo?: string;

	/**
	 * 타이틀
	 */
	readonly title?: string;

	/**
	 * 링크
	 */
	readonly link?: string;

	/**
	 * 투명 여부
	 */
	readonly transparent?: boolean;

	/**
	 * 메뉴 노드
	 */
	readonly menu?: ReactNode;
}

/**
 * 헤더 molecule 컴포넌트 반환 메서드
 *
 * @param {HeaderProps} param0: HeaderProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function Header({ logo, title, link = '/', transparent, menu, children, className, ...props }: HeaderProps): React.JSX.Element
{
	return (
		<header
			className={cn('fixed top-0 left-0 flex w-full items-center gap-4 px-8 py-2 duration-300', {
				'bg-white/80 shadow backdrop-blur dark:bg-black/80': transparent !== true,
				'text-white': transparent === true
			}, className)}
			data-component="Header"
			{...props}
		>
			{menu}

			<Link className="inline-flex items-center gap-4" href={link}>
				{logo === undefined ? null : (
					<div className="shadow">
						<Img alt={logo} height={24} src={logo} width={24} />
					</div>
				)}

				{title === undefined ? null : (
					<div>
						<Typography className="font-header" variant="h4">{title}</Typography>
					</div>
				)}
			</Link>

			{children}
		</header>
	);
}