'use client';

import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { ReactQueryDevtools } from '@tanstack/react-query-devtools';
import React, { useMemo } from 'react';

import type { ComponentProps, PropsWithChildren } from 'react';

export interface QueryProviderProps extends PropsWithChildren
{
	/**
	 * 기본 react-query 객체
	 */
	readonly client?: QueryClient;

	/**
	 * 버튼 위치
	 */
	readonly buttonPosition?: ComponentProps<typeof ReactQueryDevtools>['buttonPosition'];
}

/**
 * react-query 프로바이더 organism 컴포넌트 반환 메서드
 *
 * @returns {React.JSX.Element} JSX
 */
export default function QueryProvider({ client, buttonPosition, children }: QueryProviderProps): React.JSX.Element
{
	const obj = useMemo(() => client ?? new QueryClient({ defaultOptions: { queries: { staleTime: 3600 * 1000 } } }), [ client ]);

	return (
		<QueryClientProvider client={obj}>
			{children}

			<ReactQueryDevtools buttonPosition={buttonPosition} />
		</QueryClientProvider>
	);
}