'use client';

import { type PropsWithChildren, useLayoutEffect } from 'react';

import { themeStore } from '@kapoo/state';

export type ThemeProviderProps = PropsWithChildren;

/**
 * 테마 프로바이더 organism 컴포넌트 반환 메서드
 *
 * @param {ThemeProviderProps} param0: ThemeProviderProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function ThemeProvider({ children }: ThemeProviderProps): React.JSX.Element
{
	const { themeState, setThemeState } = themeStore();

	useLayoutEffect(() =>
	{
		const theme = localStorage.getItem('theme');

		// 테마가 유효할 경우
		if (theme === 'light' || theme === 'dark')
		{
			setThemeState(theme);
		}
	}, [ setThemeState ]);

	useLayoutEffect(() =>
	{
		document.body.classList.remove('light', 'dark');
		document.body.classList.add(themeState);
	}, [ themeState ]);

	return (
		// eslint-disable-next-line @react/jsx-no-useless-fragment
		<>{children}</>
	);
}