'use client';

import { Menu } from 'lucide-react';
import { usePathname } from 'next/navigation';
import React, { useEffect, useState } from 'react';

import Button from '@kapoo/headless-ui-pack/components/new-york/Button';
import Sheet from '@kapoo/headless-ui-pack/components/new-york/Sheet';

import Header from '../../molecule/Header';
import Sidebar from '../../molecule/Sidebar';

import type { HeaderProps } from '../../molecule/Header';
import type { SidebarProps } from '../../molecule/Sidebar';

type HeaderPickProps = Pick<HeaderProps, 'logo' | 'title' | 'link' | 'transparent' | 'className'>;

type SidebarPickProps = Pick<SidebarProps, 'children' | 'defaultOpen' | 'modal' | 'onOpenChange' | 'open' | 'side'>;

export type NavigationProps = HeaderPickProps & SidebarPickProps;

/**
 * 네비게이션 organism 컴포넌트 반환 메서드
 *
 * @param {NavigationProps} param0: NavigationProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function Navigation({ logo, title, link, transparent, className, ...props }: NavigationProps): React.JSX.Element
{
	const [
		isOpenState,
		setIsOpenState
	] = useState(false);

	const pathname = usePathname();

	useEffect(() =>
	{
		setIsOpenState(false);
	}, [ pathname ]);

	return (
		<Sidebar
			open={isOpenState}
			trigger={(
				<Header
					className={className}
					link={link}
					logo={logo}
					menu={(
						<Sheet.Trigger asChild>
							<Button size="icon" variant="ghost">
								<Menu />
							</Button>
						</Sheet.Trigger>
					)}
					title={title}
					transparent={transparent}
				/>
			)}
			onOpenChange={setIsOpenState}
			{...props}
		/>
	);
}